/* Box sizing rules */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
    margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
    list-style: none;
}

/* Set core root defaults */
html:focus-within {
    scroll-behavior: smooth;
}

/* Set core body defaults */
body {
    min-height: 100vh;
    text-rendering: optimizeSpeed;
    line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
    text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
    max-width: 100%;
    display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
    font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
    html:focus-within {
        scroll-behavior: auto;
    }

    *,
    *::before,
    *::after {
        animation-duration: 0.01ms !important;
        animation-iteration-count: 1 !important;
        transition-duration: 0.01ms !important;
        scroll-behavior: auto !important;
    }
}

html,
body,
#root
{
    height: 100%;
    overflow: hidden;
}

#shop-content, #home-content {
    height: 100%;
}

#header, #header-shop {
    background: rgb(255, 255, 255);
    background: linear-gradient(90deg, rgba(255, 255, 255, 0.6) 0%, rgba(255, 255, 255, 0) 70%);
    height: 15%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 1rem;
    padding-right: 3rem;
}

#header-shop {
    background: rgb(255, 255, 255);
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 100%);
}

#logo-home, #logo-shop {
    font-size: 8rem;
    font-family: 'Playfair Display', serif;
    padding: 0;
}

#logo-home {
    color: black;
}

#logo-shop {
    color: white;
}

#header-right {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 40%;
}

button {
    background-color: transparent;
    border: none;
    font-size: 2.5rem;
    font-family: 'Playfair Display', serif;
    cursor: pointer;
    width: auto;
}

button:hover {
    color: gray;
}

#shopping-cart-button {
    background-image: url('https://cdn-icons-png.flaticon.com/512/263/263142.png');
    background-repeat: no-repeat;
    background-size: contain;
    height: 3rem;
    width: 3rem;
}

#shopping-cart-button:hover {
    filter: invert(0.5)
}

.white {
    filter: invert(1);
}

#home-content, #home-content-small {
    background-image: url('https://images7.alphacoders.com/408/408462.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    transition: width 2s;
}

#home-content {
    width: 100%;
}

#home-content-small {
    width: 80%;
}

#shop-content, #shop-content-small {
    overflow: hidden;
    transition: width 2s;
    height: 100%;
    background-image: url('https://images.unsplash.com/photo-1635447697416-77dd49b90856?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1470&q=80');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

#shop-content {
    width: 100%;
}

#shop-content-small {
    width: 80%;
}

#products-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    row-gap: 4rem;
    justify-items: center;
    align-items: center;
    height: 80%;
    overflow: scroll;
    margin: 2rem;
    padding: 2rem;
}

#product-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 20rem;
    width: 20rem;
    font-size: 1.5rem;
    font-family: 'Playfair Display', serif;
    border: double;
    border-width: 0.5rem;
    background-color: rgba(255, 255, 255, 0.3);
}

#product-card-shopping-cart {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 1rem;
    position: relative;
    min-height: 6rem;
    width: 90%;
    font-size: 1rem;
    font-family: 'Playfair Display', serif;
    background-color: rgba(218, 218, 218, 0.5);
    white-space: nowrap;
    border: double;
}

#remove-button {
    position: absolute;
    bottom: 80%;
    right: 0;
    font-size: 1rem;
    font-weight: bold;
    color: rgb(150, 0, 0);
}

.sushi-img {
    height: 8rem;
    width: 8rem;
}

.sushi-img-shopping-cart {
    height: 3rem;
    width: 3rem;
}

.item-count-form {
    display: flex;
    justify-content: center;
    align-items: center;
}

input {
    height: 2rem;
    width: 4rem;
    font-family: 'Playfair Display', serif;
    text-align: center;
    background: transparent;
    border: none;
    border-bottom: double;
}

input[type=number] {
    -moz-appearance: textfield;
}

#shopping-cart-visible, #shopping-cart-hidden {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 2rem;
    height: 100%;
    width: 20%;
    position: absolute;
    background-color: white;
    right: 0;
    top: 0;
    transition: transform 2s;
    padding-top: 2rem;
    padding-bottom: 2rem;
    overflow: scroll;
}

#shopping-cart-hidden {
    transform: translateX(100%);
}

#shopping-cart-visible {
    transform: translateX(0);
}

#total-text {
    font-family: 'Playfair Display', serif;
}

#contact-page, #contact-page-small {
    background-image: url('https://images.unsplash.com/photo-1583623025817-d180a2221d0a?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1471&q=80');
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    position: relative;
    transition: width 2s;
}

#contact-page {
    width: 100%;
}

#contact-page-small {
    width: 80%;
}

#contact-content, #contact-content-small {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin-top: 12rem;
}

#contact-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    font-size: 4rem;
    color: white;
    font-family: 'Playfair Display', serif;
    background-color: rgb(0, 0, 0, 0.5);
    border: double;
    padding: 1rem;
}